<template>
	<view>
		<view class="cont">
		      <view class="form">
				  <image :src="avatarUrl" mode=""></image>
		        <image src="/static/img/yonghu.png" alt="" />
		        <view><input type="text" placeholder="用户名" /></view>
		      </view>
		      <view class="form">
		        <image src="/static/img/yaoshi.png" alt="" style="padding: 4px" />
		        <view><input type="password" placeholder="密码" /></view>
		      </view>
		    </view>
		    <view class="btn">
				<!-- <button class="login" open-type="getUserProfile" @getuserProfile="bindGetUserInfo">登录</button> -->
		      <button class="login" @click="weixinLogin">登&nbsp;录</button>
		      <view class="rgst">若无账号请注册</view>
		    </view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'Hello',
				userInfo: [],
				avatarUrl: ''
			};
		},
		methods: {
			bindGetUserInfo(res){
				console.log(res);
				if(res.detail.userInfo) {
					this.avatarUrl= res.detail.userInfo.avatarUrl;
					console.log(res.detail.userInfo.nickName);
				}
			},
			weixinLogin() {
							uni.showLoading({
								title: '登录中',
								icon: 'loading'
							})
							let that = this;
							uni.getProvider({
								service: 'oauth',
								success: function(res) {
									//支持微信、qq和微博等
									if (~res.provider.indexOf('weixin')) {
			 
										uni.login({
											provider: 'weixin',
											success: function(loginRes) {
												// let access_token = loginRes.authResult.access_token
												// let openid = loginRes.authResult.openid
			 
												// 获取用户信息
												uni.getUserProfile({
													provider: 'weixin',
													success: function(infoRes) {
														let data = Object.assign(loginRes
															.authResult, infoRes);
														that.$store.dispatch('Login', {
															type: 'weixin',
															data
														}).then(res => {
															if (res == 'ok') {
																uni.hideLoading()
															}
														}).catch(err => {
															uni.hideLoading();
															uni.showToast({
																icon: 'none',
																title: err
															})
														})
													},
													fail: function(err) {
														uni.showToast({
															icon: 'none',
															title: err
														})
													}
												})
											},
											fail: function(err) {
												uni.showToast({
													icon: 'none',
													title: err
												})
											}
										});
			 
									}
								},
							})
						},
		}
	};
</script>

<style>
.cont {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translateX(-50%);
  background-color: #fff;
  width: 270px;
  height: 300px;
}
.form {
  display: flex;
  justify-content: center;
}
.form image {
  /* background-color: #333; */
  width: 40px;
  height: 40px;
  margin-right: 10px;
}
.form:nth-child(2) image {
	width: 32px;
	height: 32px;
}
.form input {
	height: 40px;
  border: none;
  border-bottom: 1px solid #ffa800;
}
/* 按钮 */
.btn {
  position: absolute;
  bottom: 30%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.btn .login {
  border-radius: 19px;
  width: 150px;
  background-color: #ffa800;
  color: #fff;
  font-size: 18px;
  margin-bottom: 20px;
}
.btn .rgst {
  border-bottom: 1px solid #ffa800;
  width: 110px;
  color: #666;
  text-align: center;
  line-height: 16px;
  font-size: 14px;
}
/* 按钮激活样式 */
.active {
  border: 1px solid #333;
  background: #000;
  color: white;
}
</style>
